import React from "react"
import {CSSTransition} from "react-transition-group"
import {skin, setSkinStyle} from "../../util/skin"

import "./skin.styl"

class Skin extends React.Component {
	constructor(props) {
		super(props);
		this.skins = [
          {key: "LemonYellow", name: "Lemmon Yellow", color: "#FFD700"},
          {key: "coolBlack", name: "Cool Black", color: "#212121"},
          {key: "kuGouBlue", name: "KuGou Blue", color: "#2CA2F9"},
          {key: "netEaseRed", name: "NetEase Red", color: "#D43C33"},
          {key: "QQGreen", name: "QQ Green", color: "#31C27C"}
        ]
	}
	setCurrentSkin = (key) => {
        setSkinStyle(skin[key]);
        this.props.setSkin(key);
        this.props.close();
	}
	render() {
		return (
			<CSSTransition in={this.props.show} timeout={300} classNames="pop"
				onEnter={() => {
					this.refs.skin.style.display = "block";
				}}
				onExited={() => {
					this.refs.skin.style.display = "none";
				}}>
				<div className="music-skin" ref="skin">
			    	<div className="header">
			        	Skins
			        	<span className="cancel" onClick={() => {this.props.close();}}>Cancel</span>
			      	</div>
			      	<div className="skin-title">Recommended skins</div>
			      	<div className="skin-container">
			      		{
			      			this.skins.map(skin => (
			      				<div className="skin-wrapper" onClick={() => {this.setCurrentSkin(skin.key);}} key={skin.key}>
					          		<div className="skin-color" style={{backgroundColor: skin.color, boxShadow: `0 0 3px ${skin.color}`}}>
						            	<i className="icon-right" style={{display: skin.key === this.props.currentSkin ? "" : "none"}}></i>
						          	</div>
					          		<div>{skin.name}</div>
					          	</div>
			      			))
			      		}
			      </div>
			    </div>
		    </CSSTransition>
		);
	}
}

export default Skin


